<template>
<div>
  <div class="header">
    <a href="" class="logo">MuonN</a>
    <div class="menu-toggle" v-on:click="change" :class="{active: active}"></div>
    <nav :class="{active:active}">
      <ul>
        <li><a @click="goHome()" class="active">Home</a></li>
        <li><a @click="goProfile()">Profile</a></li>
        <li><a @click="goService()">Service</a></li>
        <li><a @click="goTeam()">Team</a></li>
        <li><a @click="goContact()">Contact</a></li>
      </ul>
    </nav>
    <div class="clearfix"></div>
  </div>
  <router-view></router-view>
  <wave-header></wave-header>
  <card class="service"></card>
  <bottom-footer></bottom-footer>
</div>
</template>

<script>
import card from '../body/card'
import wave2 from '../header/wave2'
import footer from '../footer/footer'

export default {
  name: 'navbar',
  components: {
    'wave-header': wave2,
    'card': card,
    'bottom-footer': footer
  },
  data () {
    return {
      active: false
    }
  },
  methods: {
    change () {
      if (this.active === true) {
        this.active = false
      } else {
        this.active = true
      }
    },
    goHome () {
      this.$router.push({path:'/home'})
    },
    goService () {
      this.$router.push({path: '/service'})
    },
    goContact () {
      console.log(this.$router)
      this.$router.push('/contact')
    }
  }
}
</script>

